.accessories {
	padding-top: 144px;
	padding-left: 18px;
	padding-right: 18px;
	margin: 0 auto;
	width: 100%;
	background-image: url(/src/images/accessories/bg-mob-1x.jpg), url(/src/images/accessories/bg-mob-1x.jpg);

	@media screen and(min-width:768px) {
		padding-top: 256px;
		background-image: url(/src/images/accessories/bg-tab-1x.jpg), url(/src/images/accessories/bg-tab-1x.jpg);
	}
	@media screen and(min-width:1280px) {
		padding-top: 256px;
		background-image: url(/src/images/accessories/bg-d-1x.jpg), url(/src/images/accessories/bg-d-1x.jpg);
	}
}
.img {
	position: relative;
	display: block;
	max-width: 100%;
	height: auto;
}

.accessories-items {
	display: flex;
	flex-wrap: wrap;
	gap: 24px;
	justify-content: center;
	margin-bottom: 32px;
	@media screen and(min-width:768px) {
		margin-bottom: 74px;
	}
	@media screen and(min-width:1280px) {
		margin-bottom: 72px;
	}
}
.accessories-button {
	width: 124px;
	height: 35px;
	font-weight: 700;
	font-size: 16px;
	line-height: 1.18;
	text-align: center;
	color: #86e543;
	background: transparent;
	cursor: pointer;
	border: 1px solid #86e543;
	border-radius: 16px;
	&:focus,
	&:hover {
		color: #01383a;
		background: #86e543;
		box-shadow: 0px 4px 4px rgba(134, 229, 67, 0.2);
		box-shadow: 0px 4px 4px rgba(134, 229, 67, 0.2);
	}
}

.accessories-title {
	font-weight: 600;
	font-size: 24px;
	line-height: 1.16;
	color: #ffffff;
	margin-left: 16px;
	margin-bottom: 27px;
	@media screen and( min-width:768px) {
		padding-left: 142px;
		font-weight: 600;
		font-size: 56px;
		line-height: 1.17;
	}
}
.accessories-list {
	@media screen and(max-width: 479px) {
		margin-bottom: 20px;
	}
	@media screen and(min-width:768px) {
		display: flex;
		flex-wrap: wrap;
		gap: 20px;
		row-gap: 32px;
		justify-content: center;
	}
	@media screen and(min-width:1280px) {
		display: flex;
		flex-wrap: wrap;
		justify-content: center;
		gap: 20px;
		row-gap: 24px;
	}
}
