.accessories-wrap {
	background: linear-gradient(0deg, #01383a, #01383a);
	border: 1px solid #86e543;
	box-shadow: 0px 4px 4px rgba(134, 229, 67, 0.2);
	border-radius: 12px;
	//max-width: 285px;
	//height: 276px;
	margin-bottom: 8px;
	position: relative;
	overflow: hidden;
}

.accessories-text {
	font-size: 14px;
	line-height: 1.142;
	margin-bottom: 11px;
	background-color: #01383a;
	position: absolute;
	top: 0;
	left: 0;
	color: #ffffff;
	padding: 16px 16px 52px 16px;
	height: 100%;

	transform: translateY(100%);
	transition: transform 250ms;
	transition-timing-function: cubic-bezier (0.4, 0, 0.2, 1);
	overflow: hidden;
	.accessories-link:hover &,
	.accessories-link:focus & {
		transform: translate(0);
	}
}

.accessories-subject {
	font-weight: 600;
	font-size: 22px;
	line-height: 1.18;
	color: #86e543;
	margin-bottom: 8px;
}
.accessories-price-list {
	display: flex;
	align-items: center;
	margin-bottom: 28px;
}
.accessories-old-price {
	font-weight: 600;
	font-size: 14px;
	line-height: 1;
	text-decoration-line: line-through;
	color: #8d9289;
	margin-right: 80px;
}

.accessories-new-price {
	font-weight: 600;
	font-size: 20px;
	line-height: 1;
	color: #86e543;
}
