.sale {
  background-image: url(/src/images/sale/background-mob-1x.jpg);
  width: 100%;
  @media screen and (min-width: 768px) {
    background-image: url(/src/images/sale/background-tab-1x.jpg);
  }
  @media screen and (min-width: 1280px) {
    background-image: url(/src/images/sale/background-dt-1x.jpg);
  }
}

.sale-cards {
  @media screen and (min-width: 480px) {
    width: 480px;
    margin: 0 auto;
    // padding-left: 17px;
    // padding-right: 18px;
    // padding-bottom: 48px;
  }
  @media screen and (min-width: 768px) {
    width: 768px;
    padding: 0 80px;
    margin: 0 auto;
  }
  @media screen and (min-width: 1280px) {
    width: 1280px;
    padding: 0 40px;
    margin: 0 auto;
  }
}

.mob-is-hidden {
  @media screen and (max-width: 767px) {
    display: none;
  }
}

.sale-title {
  font-weight: 600;
  font-size: 24px;
  line-height: 1.17;
  color: #ffffff;
  padding-left: 80px;
  margin-bottom: 4px;
  @media screen and (min-width: 768px) {
    font-size: 56px;
    line-height: 1.18;
    padding-left: 62px;
    // margin-bottom: 4px;
  }
  // @media screen and (min-width: 1280px) {
  //   margin-bottom: 43px;
  // }
}

.sale-list {
  @media screen and (max-width: 767px) {
    margin-bottom: 24px;
  }
  @media screen and (min-width: 768px) {
    margin-bottom: 28px;
    display: flex;
    flex-wrap: wrap;
    gap: 38px;
    row-gap: 32px;
    justify-content: center;
    // width: calc((100% - 38px) / 2);
  }
  @media screen and (min-width: 1280px) {
    gap: 20px;
    row-gap: 24px;
    margin-bottom: 24px;
  }
}

.sale-item {
  padding-bottom: 12px;
  @media screen and (max-width: 767px) {
    margin-bottom: 24px;
  }
  // @media screen and (min-width: 768px) {
  //   margin-bottom: 24px;
  // }
  // @media screen and (min-width: 1280px) {
  //   margin-bottom: 24px;
  // }
}

.sale-img {
  width: 100%;
  height: auto;
  border-radius: 12px;
  margin-bottom: 8px;
}

.product-title {
  font-weight: 600;
  font-size: 22px;
  line-height: 1.18;
  color: #86e543;
  max-width: 261px;
  padding-left: 8px;
  // padding-right: 16px;
  margin-bottom: 8px;
}

.product-prices {
  padding: 0 8px;
  display: flex;
  align-items: center;
  justify-content: space-between;
}

.old-price {
  font-weight: 600;
  font-size: 14px;
  line-height: 1;
  text-decoration-line: line-through;
  color: #8d9289;
}

.new-price {
  font-weight: 600;
  font-size: 20px;
  line-height: 1;
  color: #86e543;
}

.sale-btn {
  width: 153px;
  height: 51px;
  display: block;
  text-align: center;
  align-items: center;
  padding: 16px 0;
  border-radius: 16px;
  background-color: #ffffff;

  font-weight: 700;
  font-size: 16px;
  line-height: 1.19;
  color: #01383a;
  margin: 0 auto;
  @media screen and (min-width: 768px) {
    font-size: 28px;
    line-height: 1.18;
    width: 195px;
    height: 65px;
  }
}

.sale-btn:hover,
.sale-btn:focus {
  box-shadow: 0px 4px 20px rgba(255, 255, 255, 0.5),
    0px 4px 10px rgba(134, 229, 67, 0.1);
}

