.line-b3 {
  width: 105%;
  // height: $lineShink;
  border-top: 1px solid #000;

  box-shadow: 0px 4px 25px rgba(123, 227, 74, 0.25);
  background-color: inherit;

  @media screen and (min-width: 768px) {
    // $lineShink: 8px;
