.line-b4 {
  width: 105%;
  height: 8px;
  // margin-left: $lineShink;
  border-top: 1px solid #000;
  border-left: 1px solid #000;

  // box-shadow: 0px 4px 25px rgba(123, 227, 74, 0.25);
  background-color: inherit;

  @media screen and (min-width: 768px) {
    // $lineShink: 8px;
    height: 19px;
    // margin-left: $lineShink;
  }
}

.line-left {
  margin-left: calc(((100% - 320px) / 2) + 20px);

  @media screen and (min-width: 480px) {
    margin-left: calc(((100% - 480px) / 2) + 102px);
  }

   @media screen and (min-width: 768px) {
    margin-left: calc(((100% - 768px) / 2) + 142px);
  }

   @media screen and (min-width: 1280px) {
    margin-left: calc(((100% - 1280px) / 2) + 142px);
  }
}

.line-right {
  width: 100%;
}

.lines-title-name {
  display: inline-block;
  white-space: nowrap;
  margin-right: 5px;
  font-style: 26px;
  font-weight: 600;
  line-height: 1.0769;
  color: #01383a;
  @media screen and (min-width: 480px) {
    font-size: 44px;
    line-height: 1.1786;
  }
}